.header,
.footer,
.back-to-results,
.social-media-share,
.job-search,
.save-job {
  display: none; }

main:before {
  display: block;
  content: 'Thank you for printing our content at tate.co.uk. Please check back soon for more job opportunities.';
  margin-bottom: 1rem;
  border-bottom: 1px solid #999;
  padding: 3px 5px;
  font-size: italic; }

.job-details-meta .meta-list {
  margin-bottom: 2rem; }
  .job-details-meta .meta-list li {
    width: 50%;
    float: left; }

.mini-bio {
  margin-top: -1rem;
  margin-bottom: 2rem;
  padding: 1rem; }
  .mini-bio .mini-bio-header {
    padding: 0;
    margin: 0; }
    .mini-bio .mini-bio-header h5, .mini-bio .mini-bio-header h6 {
      padding: 0;
      margin: 0; }
  .mini-bio .mini-bio-info {
    zoom: 1;
    clear: both; }
    .mini-bio .mini-bio-info:after {
      content: '';
      display: block;
      clear: both;
      height: 1; }
    .mini-bio .mini-bio-info .mini-bio-info-section {
      float: left;
      padding-right: 2rem; }
